import { useState } from "react";


const Accordion = ({ children, title, toggleAccodion }) => {
    
    const [accordionState, setState] = useState("");

    const toggleAccordion = () => {
        accordionState === 'open'? setState(''):setState('open');
        // let className = 'cl-accordion';
        // accordionOpen = !accordionOpen;
        // accordionOpen? className = 'cl-accordion open': className = 'cl-accordion'
        // // return(className);
        console.log(accordionState);
    };

    return (
        <div className={'cl-accordion'} onClick={()=>toggleAccordion()}>
            <div className="accordion-head">
                <h1>{title}</h1>
            </div>
            {children}
        </div>
    );
    

}




export default Accordion;

